#footer-area {
    height: 35vh;
    background-color: rgb(9, 10, 99);
  }
  .spon {
    margin: 3% 0% 0% 30%;
    width: 600px;
    display: flex;
    gap: 10%;
  }
  .sp img {
    width: 100%;
    height: 100%;
  }
  .spon1 {
    margin: 3% 0% 0% 25%;
    width: 800px;
    height: 30px;
    display: flex;
    gap: 10px;
  }
  .sp1 img {
    width: 100%;
    height: 100%;
  }
  .spon2 {
    margin: 2% 0% 0% 35%;
    width: 800px;
    height: 30px;
    display: flex;
    gap: 10px;
  }
  .sp2 img {
    width: 100%;
    height: 100%;
  }
  /* last-area */
  .last-area {
    height: 13vh;
    background-color: #9e9e9ede;
  }
  .spon3 {
    margin: 2% 0% 0% 25%;
    width: 800px;
    height: 50px;
    display: flex;
    gap: 10%;
  }
  .sp3 img {
    width: 100%;
    height: 100%;
  }
  #adress-area {
    height: 25vh;
    background-color: rgb(9, 10, 99);
    color: #fff;
  }
  #adress-area ul {
    margin: 1% 0% 0% 30%;
    display: flex;
    width: 550px;
    height: 40px;
    font-size: 1rem;
    gap: 4%;  
  }
  #adress-area ul:nth-child(2){
    margin: 1% 0% 0% 39%;
    display: flex;
    width: 550px;
    height: 40px;
    font-size: 1rem;
    gap: 4%;  
  }
  #adress-area .lm {
    margin: 1% 0% 0% 25%;
    display: flex;
    width: 850px;
    height: 40px;
    font-size: 1rem;
    gap: 4%; 
  }