/* top-area */
#top-area {
    background-color: #000;
    position: relative;
    display: block;
    font-family: "Bebas Neue", sans-serif;
  }
  #top-area video {
    margin: 0% 0%;
    width: 100vw;
    height: 116%;
    background-color: #000;
    object-fit: none;
    margin-left: -6%;
    filter: brightness(0.6);
  }
  #top-area h1,
  #top-area h2,
  #top-area h4 {
    position: absolute;
    top: 40%;
    left: 0;
    width: 100vw;
    height: 12vh;
    text-align: center;
    font-size: 7rem;
    font-weight: bold;
    color: #fff;
  }
  #top-area h2 {
    top: 50%;
    line-height: 1;
  }
  #top-area h4 {
    top: 65%;
    font-size: 2.5rem;
    filter: brightness(0.85);
  }
  .wheel {
    position: absolute;
    width: 100px;
    height: 100px;
    top: 75%;
    left: 48%;
    color: #fff;
    border: 1px solid #ffffff40;
    border-radius: 50px;
  }
  
  .wheel::before {
    position: absolute;
    content: "";
    display: block;
    top: 21%;
    left: 49%;
    width: 2px;
    height: 14%;
    background-color: #fff;
    animation: wheel-ani 2s infinite;
    z-index: 1;
  }
  @keyframes wheel-ani {
    to {
      transform: translateY(100%);
    }
  }
  .wheel img {
    width: 100%;
    height: 50%;
    transform: translateY(20px);
  }