#home-area{
    padding-left: 200px;
    height: 80vh;
}
.htit{
    margin: 1% 0% 1% 10%;
    font-size: 4rem;
    font-weight: bold;
    font-family: "Bebas Neue", sans-serif;
    cursor: pointer;
}
.htit:after{
    position: absolute;
    content: '';
    display: block;
    width: 4%;
    height: 1%;
    background-color: #e1e15b;
}
.home{
    position: relative;
    display: flex;
    margin-left: 10%;
    width: 75vw;
}
.home img{
    cursor: pointer;
}
.h-ex{
    position: absolute;
    top: 10%;
    left: 50%;
    width: 500px;
    height: 400px;
    background-color: rgba(17,24,54,.795);
    color: #fff;
    cursor: pointer;
}
.h-ex h1{
    font-size: 2rem;
    margin: 1% 0% 5% 1%;
}
.h-ex h2{
    font-size: 1rem;
    margin: 10% 0% 5% 3%;
    color: #bbb3b3;
    line-height: 1.4;
}
.jump{
    position: absolute;
    top: 80%;
    left: 15%;
}
.jump a{
    font-size: 1.4rem;
    color: #3db7e4;

}
.jump a:hover{
    color:#fff;
}
