#match-area {
  background-color: #fff;
  padding-left: 150px;
  /* color: #fff; */
}
#match-area h1 {
  font-size: 12rem;
  padding-left: 25%;
  font-weight: bold;
  /* color: #fff; */
  font-family: "Bebas Neue", sans-serif;
}
#mc-area {
  width: 800px;
  height: 250px;
  margin-left: 8%;
}
#mcbox {
  display: flex;
  padding-top: 1%;
  width: 1344px;
  height: 252px;
  gap: 0%;
  justify-content: space-around;
  cursor: pointer;
  text-align: center;
  border: 1px solid #000;
  border-bottom: none;
}

.mcbox h4 {
  font-size: 2rem;
  margin-bottom: 1%;
}
.mcbox h3 {
  font-size: 2rem;
}
.mc1 {
  display: flex;
  margin: 3% 0% 0% 25%;
  width: 70px;
  height: 70px;
}

.mc1 img {
  width: 100%;
  height: 100%;
}
.mc2 {
  font-size: 1.5rem;
  font-weight: bold;
  padding: 0%;
  height: 55px;
}
.score {
  margin-top: 30%;
  width: 60px;
  height: 60px;
  font-size: 2rem;
}
.mcbox p:first-of-type {
  font-weight: bold;
  font-size: 1rem;
}
.ht {
  position: relative;
  display: flex;
  gap: 5%;
  width: 1500px;
  height: 450px;
  padding-left: 8%;
  cursor: pointer;
  color: #fff;
}
.ht img {
  width: 100%;
  height: 100%;
  filter: brightness(0.8);
}

.ht1 h4 {
  position: absolute;
  top: 75%;
  width: 45%;
  text-align: center;
  font-size: 3rem;
}
.ht2 h4 {
  position: absolute;
  top: 75%;
  text-align: center;
  font-size: 3rem;
}
