#kitbox {
  margin-top: 5%;
  width: 90vw;
  height: 50vh;
  position: absolute;
  overflow: hidden;
  /* top: 14%; */
  margin-left: 1.5%;
}

.kitbox {
  display: flex;
  flex-wrap: nowrap;
  gap: 1%;
  width: 130%;
  height: 120%;
}
.kit {
  width: 50%;
  height: 80%;
  cursor: pointer;
}
.kit:nth-child(2n) {
  transform: translateY(10%);
}
.kit img {
  border-radius: 15px;
  width: 100%;
  height: 90%;
}
.kitbox p {
  position: absolute;
  top: 73%;
  left: -9%;
  font-size: 1.4rem;
  font-weight: bold;
}

.kitbox p::after {
  content: "";
  margin: 8% 50% 0% 30%;
  width: 70%;
  height: 3px;
  background-color: rgb(9, 11, 99);
  transition: 1s;
  display: none;
}
.kit:hover p::after {
  display: block;
}
.kitbox .kit:nth-child(2n) p {
  top: 75%;
  color: #fff;
}
.swiper-button-prev {
  display: none;
}
.swiper-button-next {
  display: none;
}
.swiper-pagination {
  padding-left: 45%;
  text-align: justify;
}
